import React from 'react';
import {Panel, PanelHeader, Group, View, Avatar, Card, CardGrid, Separator, Progress,
        Div, Header, InfoRow} from "@vkontakte/vkui";

import Icon28SettingsOutline from '@vkontakte/icons/dist/28/settings_outline';
let Settings = ({id, fetchedUser, go}) => {
    console.log('fetcheduser = ', fetchedUser)
    return (
        <View id={id} activePanel={id}>
            <Panel id={id} style={{position: 'relative', height: 'calc(100vh - 70px)'}}>
                <PanelHeader>Мой профиль</PanelHeader>
                {/* <Group> */}
                    <Div style={{ display: 'flex', justifyContent: 'center'}}>
                        <Avatar src={`${ fetchedUser ? fetchedUser.photo_200 : 'https://sun9-3.userapi.com/c639822/v639822055/55abd/Y4nIaWtNYVI.jpg'}`} size={120}/>
                    </Div>
                {/* </Group> */}
                    <Div style={{ display: 'flex', justifyContent: 'center', fontWeight: 'bold', fontSize: '23px'}}>
                        {fetchedUser ? `${fetchedUser.first_name} ${fetchedUser.last_name}`: "Alexandr Zhabovich"}
                    </Div>
                    {/* <Separator/> */}
                    <Div style={{width: 'calc(100% - 32px)', textAlign: 'center', fontWeight: 'bold', }}>
                        6 УРОВЕНЬ
                    </Div>
                    <Div>
                    {/* <InfoRow header={`300/1000`}>
                        <Progress value={30}/>
                    </InfoRow> */}
                    </Div>
                    <Separator/>
                    <Group separator="hide" header={<Header>Мои характеристики</Header>}>
                        <CardGrid>
                            <Card size='1' mode='shadow' style={{width: 'calc(100% - 10px)', height: '100px', padding: '5px', borderRadius: 5}}>
                                <Header mode='secondary'  aside={<div style={{display: 'flex', fontWeight: 'bold', alignItems: 'center'}}>
                                    <div style={{marginRight: 20}}>
                                        <Progress value={40} style={{zIndex: 10, width: 130}}/>
                                    </div>
                                    <div style={{color: '#99A2AD'}}>
                                        40/100
                                    </div>      
                                </div>}>
                                    Мана
                                </Header>
                                <Separator />
                                <Header mode='secondary' aside={<div style={{display: 'flex', fontWeight: 'bold', alignItems: 'center'}}>
                                    <div style={{marginRight: 20}}>
                                        <Progress value={70} style={{zIndex: 10, width: 130}}/>
                                    </div>
                                    <div style={{color: '#99A2AD'}}>
                                        70/100
                                    </div>      
                                </div>}>
                                    Жизни
                                </Header>
                            </Card>
                        </CardGrid>
                    </Group>

                    <Div style={{position: 'absolute', bottom: 0, left: 0, width: 'calc(100% - 32px)', textAlign: 'center', fontWeight: 'bold'}}>
                        <span style={{fontSize: '25px', color: '#2975CC', marginRight:'5px'}}>100</span> место в таблице кармы
                    </Div>
                    <Div style={{position: 'absolute', top: 60, right: 0, color: '#2975CC'}} onClick={go} data-to="settings">
                        <Icon28SettingsOutline/>
                    </Div>
            </Panel>
        </View>
    )
}

export default Settings;